'use client';

import { format } from 'date-fns';
import { id as localeId } from 'date-fns/locale';
import { Eye, Loader2, Utensils } from 'lucide-react';
import { Button } from '@/components/ui/button';
import { Badge } from '@/components/ui/badge';
import {
    Table,
    TableBody,
    TableCell,
    TableHead,
    TableHeader,
    TableRow,
} from '@/components/ui/table';
import { Card, CardContent } from '@/components/ui/card';

interface RiwayatTableProps {
    loading: boolean;
    transaksis: any[]; 
    onViewDetail: (transaksi: any) => void;
}

export default function RiwayatTable({ loading, transaksis, onViewDetail }: RiwayatTableProps) {
    
    const formatRupiah = (num: number) => 
        new Intl.NumberFormat('id-ID', { style: 'currency', currency: 'IDR', minimumFractionDigits: 0 }).format(Number(num));

    const formatDate = (dateString: string) => 
        format(new Date(dateString), 'dd MMMM yyyy, HH:mm', { locale: localeId });

    return (
        <Card className="print:hidden border shadow-sm overflow-hidden">
            <CardContent className="p-0">
                <div className="rounded-md border-0">
                    <Table>
                        <TableHeader>
                            <TableRow className="bg-gray-50">
                                <TableHead className="w-[50px] text-center">No</TableHead>
                                <TableHead>Waktu</TableHead>
                                <TableHead>Kode Pesanan</TableHead>
                                <TableHead>Total Harga</TableHead>
                                <TableHead>Status</TableHead>
                                <TableHead className="text-right">Aksi</TableHead>
                            </TableRow>
                        </TableHeader>
                        <TableBody>
                            {loading ? (
                                <TableRow>
                                    <TableCell colSpan={6} className="h-24 text-center text-gray-500">
                                        <div className="flex justify-center items-center gap-2">
                                            <Loader2 className="h-5 w-5 animate-spin" /> Memuat riwayat...
                                        </div>
                                    </TableCell>
                                </TableRow>
                            ) : transaksis.length === 0 ? (
                                <TableRow>
                                    <TableCell colSpan={6} className="h-40 text-center text-gray-500">
                                        <div className="flex flex-col items-center justify-center">
                                            <Utensils className="h-8 w-8 text-gray-300 mb-2" />
                                            <p>Belum ada riwayat pesanan.</p>
                                        </div>
                                    </TableCell>
                                </TableRow>
                            ) : (
                                transaksis.map((item, index) => (
                                    <TableRow key={item.id} className="hover:bg-gray-50 transition-colors">
                                        <TableCell className="text-center text-gray-500 text-sm">
                                            {index + 1}
                                        </TableCell>
                                        
                                        <TableCell className="font-medium text-gray-600 text-xs">
                                            {formatDate(item.created_at)}
                                        </TableCell>
                                        <TableCell>
                                            <span className="font-mono font-bold text-gray-700 text-xs bg-gray-100 px-2 py-1 rounded">
                                                {item.kode_transaksi}
                                            </span>
                                        </TableCell>
                                        <TableCell className="font-bold text-[#D93F21]">
                                            {formatRupiah(item.total_harga)}
                                        </TableCell>
                                        <TableCell>
                                            {item.status === 'selesai' ? (
                                                <Badge className="bg-green-100 text-green-800 hover:bg-green-100 border-green-200 shadow-none">Selesai</Badge>
                                            ) : item.status === 'dibatalkan' ? (
                                                <Badge variant="destructive">Dibatalkan</Badge>
                                            ) : (
                                                <Badge variant="outline" className="bg-yellow-50 text-yellow-700 border-yellow-200">Pending</Badge>
                                            )}
                                        </TableCell>
                                        <TableCell className="text-right">
                                            <Button 
                                                variant="outline" 
                                                size="sm" 
                                                onClick={() => onViewDetail(item)}
                                                className="hover:bg-blue-50 hover:text-blue-600 h-8"
                                            >
                                                <Eye className="h-3.5 w-3.5 mr-1.5" /> Detail
                                            </Button>
                                        </TableCell>
                                    </TableRow>
                                ))
                            )}
                        </TableBody>
                    </Table>
                </div>
            </CardContent>
        </Card>
    );
}